.nav-menu {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  transform: translate3d(-100%, 0, 0);
  transition: all ease 300ms;
  line-height: 1.2;
  z-index: 100;
  &.is-active {
    transform: translate3d(0, 0, 0);
  }
}

.nav-menu__container {
  height: 100%;
  display: inline-block;
  background-color: #fff;
  padding-bottom: 50px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.nav-menu__auth {
  background-color: #c5e8e9;
  padding: 14px 20px;
  border-bottom: 1px solid #ffffff;

  a {
    display: inline-block;
    padding-left: 24px;
    padding-right: 20px;
    padding-top: 4px;
    padding-bottom: 4px;
    background-repeat: no-repeat;
    background-position: left 2px;

    &:first-of-type {
      background-image: url('/static/media/images/svg/lock.svg');
    }

    &:last-of-type {
      border-left: 1px solid #adc9ca;
    }
  }
}

.nav-menu__city-select {
  padding: 16px 20px;
  background-color: #c5e8e9;
  position: relative;

  &::before {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    right: 31px;
    top: 24px;
    border-top: 4px solid #809697;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
  }

  select {
    background-color: transparent;
    border: none;
    padding-left: 20px;
    background-image: url('/static/media/images/svg/marker.svg');
    background-position: left top;
    background-repeat: no-repeat;
    display: block;
    width: 100%;
    appearance: none;
    line-height: 20px;
    outline: none;
  }
}

.nav-menu__search {
  background-color: #40b3b7;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  input {
    padding-left: 8px;
    padding-right: 8px;
    background-color: transparent;
    border: none;
    color: #b3e1e2;
    outline: none;

    &::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: #b3e1e2;
      opacity: 1;
      /* Firefox */
    }

    &:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #b3e1e2;
    }

    &::-ms-input-placeholder {
      /* Microsoft Edge */
      color: #b3e1e2;
    }
  }

  button[type="submit"] {
    width: 50px;
    height: 50px;
    background-image: url('/static/media/images/svg/search-icon.svg');
    background-repeat: no-repeat;
    background-position: center;
    border: none;
    background-color: transparent;
  }
}

.nav-menu__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #fff;
  
  li {
    position: relative;
    border-bottom: 1px solid #ccc;
    i {
      position: absolute;
      top: 15px;
      left: 16px;
      width: 20px;
      height: 20px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
  
    a {
      position: relative;
      padding-left: 48px;
      padding-top: 16px;
      padding-bottom: 16px;
      display: block;
  
      &::after {
        content: "";
        transition: transform 150ms ease;
        position: absolute;
        top: 20px;
        right: 15px;
        display: block;
        width: 6px;
        height: 11px;
        background-image: url('/static/media/images/svg/arr-right.svg');
        background-size: 100%;
      }
    }
  }
}


.nav-menu__link-with-nested {
  font-size: 16px;

  &.is-active {
    ul {
      display: block;
    }
    a {
      &::after {
        transform: rotate(90deg);
      }
    }
  }
  ul {
    display: none;
    padding: 0;
    margin: 0;
    list-style-type: none;
    padding-left: 20px;
    display: none;
    li {
      font-size: 14px;
      &:last-of-type {
        border-bottom: none;
      }
      &:first-of-type {
        border-top: 1px solid #ccc;
      }
      a {
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 32px;
        
        &::after {
          display: none;
        }
      }
    }
  }
}


.nav-menu__feedback {
  a {
    display: block;
    padding: 16px 16px 16px 48px;
    background-color: #ffa601;
    color: #fff;
    font-weight: bold;
  }
}